<script>
import Vue from "vue";
import VueAMap from "vue-amap";
import citypicker from "@/components/common/citypicker.vue";
import address from "@/components/common/address.json";
import { calculateDistance, getAddress } from "@/utils/amap";



let amapManager = new VueAMap.AMapManager();

export default {
  props: {
    type: String
  },
  data() {
    return {
      testData: [],
      cityPickerBool: false,
      mapCenterNoteBoxBool: false,
      detailedAddress: "",
      mapShowDetailedAddress: "---",
      searchListBool: false,
      bool: false,
      adcode: null,
      contact: {
        address: "",
        name: "",
        phone: ""
      },
      currentPosition: null,
      keyword: "",
      amapManager,
      events: {
        init: async (o) => {
          this.currentPosition = o.getCenter();
          o.getCity(result => {
            console.log(result)
          });
          let map = this.$refs.amap.$$getInstance();
          let position = map.getCenter();
          let { regeocode } = await getAddress(position);
          this.contact.address = regeocode.formattedAddress;
          this.adcode = regeocode.addressComponent.adcode;
          console.log(regeocode);
        },
        moveend: async () => {
          let map = this.$refs.amap.$$getInstance();
          let position = map.getCenter();
          let { regeocode } = await getAddress(position);
          this.contact.address = regeocode.formattedAddress;
          this.adcode = regeocode.addressComponent.adcode;
          console.log(regeocode);
        }
      }
    }
  },
  components: {
    citypicker
  },
  created() {
    if (this.type == "consignee") {
      this.contact.name = this.$store.state.order.consignee.name;
      this.contact.phone = this.$store.state.order.consignee.phone;
    } else if (this.type == "consignor") {
      this.contact.name = this.$store.state.order.consignor.name;
      this.contact.phone = this.$store.state.order.consignor.phone;
    }
  },
  methods: {
    // 点击搜索结果方法
    onSearchLi(location) {
      // this.center = [location.lng, location.lat];
      this.adMap();
      this.search_key = "";
    },
    back() {
      this.$router.go(-1);
    },
    toMap() {
      if (this.bool) {
        this.bool = false;
      } else {
        this.back();
      }
    },
    toPath(url) {
      this.$router.push(url);
    },
    getRecord() {
      this.bool = true;
    },
    clearSearch() {
      this.search_key = "";
      this.bool = true;
    },
    deleteInput() {
      if (this.search_key.length === 1) {
        this.clearSearch();
      }
    },
    // toUnloadMap() {
    //   this.toPath("/unloadmap");
    //   let senderInfo = {
    //     senderAddress: "",
    //     senderName: "",
    //     senderPhone: ""
    //   };
    //   localStorage.setItem("senderInfo", senderInfo);
    // },

    // 拖动地址改变内容
    dragMapAddressToDetails() {
      this.detailedAddress = this.dragAddress[0].address;
      //  || this.$store.state.location.addressComponent.district + this.$store.state.location.addressComponent.township + this.$store.state.location.addressComponent.street + this.$store.state.location.addressComponent.streetNumber
    },

    // 点击搜索结果地图跳到指定位置
    mapToThisAddress(item) {
      this.searchListBool = false;
      this.bool = false;
      this.search_key = "";
      // this.center = [item.location.lng, item.location.lat];
      this.adMap();
      this.userSearchAddressInfo = item;
      console.log(this.userSearchAddressInfo);
      this.senderAddLng = item.location.lng;
      this.senderAddLat = item.location.lat;
      this.getCompeleteAddress(item);
    },

    clearDetailedAddress() {
      this.detailedAddress = "";
    },

    // 用户输入地址
    getUserInputAddress() {
      // console.log(111)
    },

    cityPicker() {
      this.$children[1].cityPickerBool = true;
    },

    backCurrentPosition() {
      this.$refs.amap.$$getInstance().setCenter(this.currentPosition);
    },

    
    async ok() {
      let { lat, lng } = this.$refs.amap.$$getInstance().getCenter();
      let data = {
        adcode: this.adcode,
        lat,
        lng,
        ...this.contact
      };
      console.log("this.type>>>", this.type);
      switch (this.type) {
        case "consignee":
          this.$store.commit("setConsignee", data);
          break;
        case "consignor":
          this.$store.commit("setConsignor", data);
          break;
      }
      this.$router.go(-1);
    }
  },

  watch: {
    
  }
};
</script>

